import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { MainAdminComponent } from './main-nav.component';

import { MatButtonModule } from '@angular/material/button';
import { MatCardModule } from '@angular/material/card';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatProgressBarModule } from '@angular/material/progress-bar';

import { DragDropModule } from '@angular/cdk/drag-drop';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { CdkTableModule } from '@angular/cdk/table';
import { CdkTreeModule } from '@angular/cdk/tree';
import { A11yModule } from '@angular/cdk/a11y';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatBadgeModule } from '@angular/material/badge';
import { MatBottomSheetModule } from '@angular/material/bottom-sheet';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatChipsModule } from '@angular/material/chips';
import { MatStepperModule } from '@angular/material/stepper';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatDialogModule } from '@angular/material/dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatGridListModule } from '@angular/material/grid-list';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatListModule } from '@angular/material/list';
import { MatMenuModule } from '@angular/material/menu';
import { MatNativeDateModule, MatRippleModule } from '@angular/material/core';
import {
  MatPaginatorModule,
  MatPaginatorIntl,
} from '@angular/material/paginator';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatRadioModule } from '@angular/material/radio';
import { MatSelectModule } from '@angular/material/select';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatSliderModule } from '@angular/material/slider';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatSortModule } from '@angular/material/sort';
import { MatTableModule } from '@angular/material/table';
import { MatTabsModule } from '@angular/material/tabs';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatTreeModule } from '@angular/material/tree';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

const routes: Routes = [
  {
    path: '',
    component: MainAdminComponent,

    children: [
      {
        path: '',
        redirectTo: 'dashboard',
        pathMatch: 'full',
      },
      {
        path: 'dashboard',
        loadChildren: () =>
          import('./dashboard/dashboard.module').then(
            (m) => m.DashboardModule
          ),
      },
      {
        path: 'quan-ly-thiet-bi',
        loadChildren: () =>
          import('./thiet-bi/quan-ly-thiet-bi/quan-ly-thiet-bi.module').then(
            (m) => m.QuanLyThietBiModule
          ),
      },
      {
        path: 'quan-ly-yeu-cau-su-dung-ptn',
        loadChildren: () =>
          import(
            './su-dung-ptn/quan-ly-yeu-cau-su-dung-ptn/quan-ly-yeu-cau-su-dung-ptn.module'
          ).then((m) => m.QuanLyYeuCauSuDungPTNModule),
      },
      {
        path: 'quan-ly-yeu-cau-muon-thiet-bi',
        loadChildren: () =>
          import(
            './thiet-bi/quan-ly-yeu-cau-muon-thiet-bi/quan-ly-yeu-cau-muon-thiet-bi.module'
          ).then((m) => m.QuanLyYeuCauMuonThietBiModule),
      },
      {
        path: 'lich-su-su-dung-ptn',
        loadChildren: () =>
          import(
            './su-dung-ptn/lich-su-su-dung-ptn/lich-su-su-dung-ptn.module'
          ).then((m) => m.LichSuSuDungPTNAdminModule),
      },
      {
        path: 'quan-ly-nguoi-hoc',
        loadChildren: () =>
          import('./thiet-bi/lich-su/quan-ly-nguoi-hoc.module').then(
            (m) => m.QuanLyNguoiHocModule
          ),
      },
      {
        path: 'quan-ly-tai-khoan',
        loadChildren: () =>
          import('./quan-ly-tai-khoan/quan-ly-tai-khoan.module').then(
            (m) => m.QuanLyTaiKhoanModule
          ),
      },
      {
        path: 'quan-ly-noi-san-xuat',
        loadChildren: () =>
          import('./quan-ly-noi-san-xuat/quan-ly-noi-san-xuat.module').then(
            (m) => m.QuanLyNoiSanXuatModule
          ),
      },
      {
        path: 'quan-ly-noi-truc-thuoc',
        loadChildren: () =>
          import('./quan-ly-noi-truc-thuoc/quan-ly-noi-truc-thuoc.module').then(
            (m) => m.QuanLyNoiTrucThuocModule
          ),
      },
      {
        path: 'quan-ly-bieu-mau',
        loadChildren: () =>
          import('./quan-ly-bieu-mau/quan-ly-bieu-mau.module').then(
            (m) => m.QuanLyBieuMauModule
          ),
      },
      {
        path: 'quan-ly-noi-dung-lien-he',
        loadChildren: () =>
          import(
            './quan-ly-noi-dung-lien-he/quan-ly-noi-dung-lien-he.module'
          ).then((m) => m.QuanLyNoiDungLienHeModule),
      },
      {
        path: 'quan-ly-thong-so-mac-dinh',
        loadChildren: () =>
          import(
            './quan-ly-thong-so-mac-dinh/quan-ly-thong-so-mac-dinh.module'
          ).then((m) => m.QuanLyThongSoMacDinhModule),
      },
    ],
  },
];
@NgModule({
  imports: [
    RouterModule.forChild(routes),
    CommonModule,
    MatProgressBarModule,
    MatCardModule,
    MatFormFieldModule,
    FormsModule,
    MatButtonModule,
    MatToolbarModule,
    MatSidenavModule,
    MatListModule,
    MatExpansionModule,
    MatIconModule, 
  ],
  declarations: [MainAdminComponent],
})
export class MainAdminModule {}
